"use client";

import React, { useMemo, useState } from "react";
import { useRouter, useSearchParams } from "next/navigation";
import { ZusText, ZusButton } from "@/components/zus-ui";
import { ZusInput } from "@/components/ui/zus-input";
import { PueLoginPanel } from "@/components/pue/pue-panel";

// Rough % for emerytalna składka (employee+employer); purely indicative for the UI hint.
const PENSION_RATE = 0.1952;

function monthsSinceStartYear(startYear: number) {
  const now = new Date();
  const y = now.getFullYear();
  const m = now.getMonth() + 1; // 1..12
  const totalNow = y * 12 + m;
  const totalStart = startYear * 12 + 1; // assume Jan of start year
  return Math.max(0, totalNow - totalStart);
}

export default function ExtraDataPage() {
  const router = useRouter();
  const params = useSearchParams();

  // read prior screen (best-effort)
  const age = Number(params.get("age") ?? "30");
  const sex = params.get("sex") ?? "M";
  const salary = Number(params.get("grossSalary") ?? "9000");
  const startYear = Number(params.get("workStartYear") ?? `${new Date().getFullYear() - 6}`);
  const retireYear = Number(params.get("retireYear") ?? `${new Date().getFullYear() + 35}`);

  // --- local state
  const [useEstimatedFunds, setUseEstimatedFunds] = useState<boolean>(true);
  const [fundsNow, setFundsNow] = useState<number | "">("");
  const [sickDays12m, setSickDays12m] = useState<number | "">("");

  const estimatedFunds = useMemo(() => {
    if (!Number.isFinite(salary) || !Number.isFinite(startYear)) return 0;
    const months = monthsSinceStartYear(startYear);
    // extremely rough: gross * months * 19.52%
    return Math.max(0, Math.round(salary * months * PENSION_RATE));
  }, [salary, startYear]);

  const isValid = useMemo(() => {
    // Nothing is strictly required here. We allow skipping.
    // If not using estimate, require a non-negative number for funds.
    if (!useEstimatedFunds) {
      return fundsNow !== "" && Number.isFinite(Number(fundsNow)) && Number(fundsNow) >= 0;
    }
    return true;
  }, [useEstimatedFunds, fundsNow]);

  function goPredict() {
    const q = new URLSearchParams({
      age: String(age),
      sex,
      grossSalary: String(Math.round(Number(salary))),
      workStartYear: String(startYear),
      retireYear: String(retireYear),
      // extras
      fundsNow: String(
        useEstimatedFunds ? estimatedFunds : Math.round(Number(fundsNow || 0))
      ),
      fundsSource: useEstimatedFunds ? "estimated" : "user",
      sickDays12m: String(Number(sickDays12m || 0)),
    });
    router.push(`/simulator?${q.toString()}`);
  }

  function skipAndUseEstimates() {
    // Force estimates + zero sick days if user wants to skip
    const q = new URLSearchParams({
      age: String(age),
      sex,
      grossSalary: String(Math.round(Number(salary))),
      workStartYear: String(startYear),
      retireYear: String(retireYear),
      fundsNow: String(estimatedFunds),
      fundsSource: "estimated",
      sickDays12m: "0",
    });
    router.push(`/simulator?${q.toString()}`);
  }

  return (
    <div className="bg-white rounded-2xl">
      <div className="grid grid-cols-1 md:grid-cols-17">
        {/* LEFT */}
        <div className="p-6 md:p-8 flex flex-col gap-6 md:col-span-8">
          <div>
            <h2 className="mt-2 text-[22px] leading-7 font-semibold text-[rgb(var(--zus-black))]">
              Dane uzupełniające
            </h2>
            <ZusText className="mt-2">
              Te informacje zwykle nie są wprost dostępne w PUE ZUS. Podaj je, aby
              poprawić dokładność prognozy. Jeśli ich nie znasz – możemy użyć prostych
              szacunków na podstawie Twojej pensji i stażu pracy.
            </ZusText>
          </div>

          {/* Funds now */}
          <div className="space-y-3">
            <div className="flex items-center justify-between gap-4">
              <label htmlFor="fundsNow" className="block text-[14px] font-medium text-neutral-800">
                Zgromadzona kasa na kontach (PLN)
              </label>
              <label className="inline-flex items-center gap-2 text-sm text-neutral-700 select-none">
                <input
                  type="checkbox"
                  className="h-4 w-4 rounded border border-[#2E6AA2] accent-[#2E6AA2]"
                  checked={useEstimatedFunds}
                  onChange={(e) => setUseEstimatedFunds(e.target.checked)}
                />
                <span>Oszacuj na podstawie pensji</span>
              </label>
            </div>

            {useEstimatedFunds ? (
              <div className="flex items-start justify-between gap-4">
                <div>
                  <p className="text-[15px] leading-6 text-neutral-800">
                    Szacowana kwota:{" "}
                    <span className="font-semibold">
                      {new Intl.NumberFormat("pl-PL", {
                        style: "currency",
                        currency: "PLN",
                        maximumFractionDigits: 0,
                      }).format(estimatedFunds)}
                    </span>
                  </p>
                  <p className="zus-text-small mt-1 text-gray-700">
                    Prosta metoda: <code>pensja × liczba miesięcy pracy × 19,52%</code>. To wyłącznie
                    orientacyjna wartość — dokładne dane pobierzemy z eZUS, jeśli się zalogujesz.
                  </p>
                </div>
                <ZusButton
                  variant="ghost"
                  type="button"
                  className="shrink-0"
                  onClick={() => setUseEstimatedFunds(false)}
                >
                  Podaj własną kwotę
                </ZusButton>
              </div>
            ) : (
              <div>
                <ZusInput
                  id="fundsNow"
                  label="Kwota łączna (PLN)"
                  type="number"
                  min={0}
                  step={100}
                  value={fundsNow === "" ? "" : String(fundsNow)}
                  onChange={(e) => setFundsNow(e.target.value === "" ? "" : Number(e.target.value))}
                  className="w-64"
                />
                <p className="zus-text-small mt-1 text-gray-700">
                  Opcjonalnie możesz wpisać sumę własnych środków (ZUS/OFE/IKZE/IKE itp.).
                </p>
              </div>
            )}
          </div>

          {/* Sick leaves */}
          <div className="space-y-2">
            <label htmlFor="sickDays12m" className="block text-[14px] font-medium text-neutral-800">
              Zwolnienia lekarskie (dni, ostatnie 12 mies.)
            </label>
            <div className="flex items-center gap-3">
              <ZusInput
                id="sickDays12m"
                label="Liczba dni"
                type="number"
                min={0}
                step={1}
                value={sickDays12m === "" ? "" : String(sickDays12m)}
                onChange={(e) => setSickDays12m(e.target.value === "" ? "" : Number(e.target.value))}
                className="w-40"
              />
              <span className="zus-text-small text-neutral-600">Pole opcjonalne</span>
            </div>
            <p className="zus-text-small mt-1 text-gray-700">
              Pomaga lepiej odwzorować historię składek i świadczeń.
            </p>
          </div>

          {/* Skip */}
          <div className="flex items-center justify-between pt-2">
            <button
              type="button"
              className="underline text-[13px] font-medium"
              style={{ color: "rgb(var(--zus-blue))" }}
              onClick={skipAndUseEstimates}
            >
              Pomiń i użyj szacunków
            </button>
          </div>

          {/* Big CTA */}
          <div className="sticky bottom-0 pt-2 bg-white">
            <ZusButton
              type="button"
              variant="primary"
              className="w-full h-12 text-[15px]"
              onClick={goPredict}
              disabled={!isValid}
              aria-disabled={!isValid}
            >
              ZAPROGNOZUJ MOJĄ PRZYSZŁĄ EMERYTURĘ
            </ZusButton>
          </div>
        </div>

        {/* Divider with "LUB" */}
        <div className="flex flex-col items-center justify-center my-6 md:col-span-1">
          <div className="w-px h-20 bg-gray-300"></div>
          <span className="py-4 text-sm text-gray-500 font-medium">LUB</span>
          <div className="w-px h-20 bg-gray-300"></div>
        </div>

        {/* RIGHT: eZUS panel */}
        <div className="md:col-span-8">
          <PueLoginPanel />
        </div>
      </div>
    </div>
  );
}